'use strict';

var utils = require('./utils');
var mull = require('./null');
var mndefined = require('./undefined');

module.exports = Iterator;

function Iterator(list) {
  list || (list = []);
  this.queue = list;
  this.wait = list.length;
  this.onResult = null;
  this.onError = null;
  this.shift();
}

Iterator.prototype.next = function () {
  if (this.wait === -1) {
    this.onResult(null);
    return;
  }
  this.runGenerator()
    || this.runGeneratorFunction()
    || this.runPromise()
    || this.runDeferred()
    || this.runArray()
    || this.run();
};

Iterator.prototype.shift = function () {
  this.wait -= 1;
  this.value = this.queue.shift();
};

Iterator.prototype.unshift = function () {
  this.wait = this.queue.unshift.apply(this.queue, arguments);
};

Iterator.prototype.push = function () {
  this.wait = this.queue.push.apply(this.queue, arguments);
};

Iterator.prototype.runGenerator = function () {
  var val = this.value;

  if (!utils.isGenerator(val)) {
    return;
  }

  var ret = val._last;

  if (ret && ret.done) {
    this.shift();
    this.next();
    return true;
  }

  try {
    ret = val.next();
  } catch (e) {
    this.onError(e);
    this.shift();
    return;
  }

  val._last = ret;
  this.unshift(val);

  if (!ret.done) {
    this.unshift(ret.value);
  }

  this.shift();
  this.next();

  return true;
};

Iterator.prototype.runGeneratorFunction = function () {
  var val = this.value;

  if (!utils.isGeneratorFunction(val)) {
    return;
  }

  this.value = val();
  this.runGenerator();

  return true;
};

Iterator.prototype.runPromise = function () {
  var val = this.value;

  if (!utils.isPromise(val)) {
    return;
  }

  val.then((function (result) {
    this.unshift(result);
    this.shift();
    this.next();
  }.bind(this)), this.onError);

  return true;
};

Iterator.prototype.runDeferred = function () {
  var val = this.value;

  if (!utils.isDeferred(val)) {
    return;
  }

  this.value = val.promise;
  this.runPromise();

  return true;
};

Iterator.prototype.runArray = function () {
  var val = this.value;

  if (!Array.isArray(val)) {
    return;
  }

  this.shift();
  this.next();

  return true;
};

Iterator.prototype.run = function () {
  var val = this.value;
  if (val === null) {
    val = mull;
  } else if (val === undefined) {
    val = mndefined;
  }
  this.shift();
  this.onResult(val);
};
