'use strict';

var utils = module.exports = {};

utils.isGenerator = function (obj) {
  return obj && 'function' == typeof obj.next && 'function' == typeof obj.throw;
};

utils.isGeneratorFunction = function (fn) {
  return fn && fn.constructor && 'GeneratorFunction' == fn.constructor.name;
};

utils.isPromise = function (obj) {
  return obj && 'function' == typeof obj.then;
};

utils.isDeferred = function (obj) {
  return obj && utils.isPromise(obj.promise);
};
