'use strict';

var Stream = require('./stream');
var Iterator = require('./iterator');
var mull = require('./null');
var mndefined = require('./undefined');

var toStream = module.exports = function (data, options) {
  data || (data = []);
  if (!Array.isArray(data)) {
    data = [data];
  }
  var iterator = new Iterator(data);
  return new Stream(iterator, options);
};

toStream.isNull = function (obj) {
  return obj === mull || obj === null;
};

toStream.isUndefined = function (obj) {
  return obj === mndefined || obj === undefined;
};
